<template>
  <div id="Homehtml">
    <h2 id="uesrone">用户管理</h2>
    <!-- 遮罩层 -->
    <div id="Homeshade" style="display:none" ref="refHomeshade">
      <div id="shade"></div>
      <div id="tankuang">
        <div style="text-align: center;font-size: 20px;">新建/编辑用户</div>
        <!-- 具体信息 -->
        <div id="message">
            <div style="font-size:18px"><div style="display: inline-block; width: 52%;">姓名</div><div style="display: inline-block; width: 46%;">姓别</div></div>
            <input v-model="namecontent" type="text" style="width:40%;margin-right:10%;height: 24px;font-size: 18px;">
            <select v-model="sexcontent" name="sex" style="width:40%;height: 31px;font-size: 18px;">
                <option value="男">男</option>
                <option value="女">女</option>
             </select>
            <div style="display: inline-block;width: 51.5%;font-size:18px;margin-top: 20px;">联系电话</div><div style="display: inline-block;width: 30%;font-size:18px;margin-top: 20px;">年龄</div>
            <input v-model="phonecontent" type="text" style="width:40%;margin-right:10%;height: 24px;font-size: 18px;"><input v-model="agecontent" type="text" style="width:40%;height: 24px;font-size: 18px;">
            <div style="font-size:18px;margin-top: 20px;">详细地址</div>
            <select name="sex" style="width:40%;height: 31px;font-size: 16px;color:rgba(0, 0, 0, .6);">
                <option value="i">广东省/广州市/{{createaddress}}</option>
             </select>
            <input v-model="addresscontent" type="text" style="width:48.5%;margin-left:2%;height: 24px;font-size: 18px;" placeholder="请输入详细地址">
            <!-- 地址选择栏 -->
            <div style="height:110px">
              <div style="height: 100%;position: relative;top:-70px;display:inline-block;;width: 23%;border: 1px solid #aaa;"><div style="text-align:center;background-color: #eee;">&nbsp; 广东省 ></div></div>
              <div style="height: 100%;position: relative;top:-70px;display:inline-block;width: 23%;border: 1px solid #aaa;border-left: none;"><div style="text-align:center;background-color: #eee;">&nbsp; 广州市 ></div></div>
              <div style="height: 100%;position: relative;top:13px;display:inline-block;width: 23%;border: 1px solid #aaa;border-left: none;">
                <div @click="createaddressfn">
                  <div :class="{AddressActive: isActive === 1}" style="text-align:center;">番禺区</div>
                  <div :class="{AddressActive: isActive === 2}" style="text-align:center;">越秀区</div>
                  <div :class="{AddressActive: isActive === 3}" style="text-align:center;">花都区</div>
                  <div :class="{AddressActive: isActive === 4}" style="text-align:center;">海珠区</div>
                  <div :class="{AddressActive: isActive === 5}" style="text-align:center;">天河区</div>
                </div>
              </div>
            </div>
            <!-- 保存和返回 -->
            <div style="position: relative;top: 20px;left: 75%;">
              <button @click="Homecanceldelete" class="Bcactive" style="outline:none;border:none;width: 12%;">取消</button>
              <button @click="Homecancel" class="Bcactive" style="outline:none;border:none;margin-left:1%;width: 12%">保存</button>
            </div>
        </div>
      </div>
    </div>
    <!-- 内层 -->
    <div id="Homebody">
      <div style="height:25px">
      <button @click="Homecreat" style="float: left; margin-right: 20px;width: 80px;" >新建</button>
      <input v-model="Homemassge" @blur="shousuo" @keyup = "shousuo" style="width: 250px;float: left" type="text" placeholder="按关键字搜索" name="username">
      <button @click="Homerevoke" style="float: right;width: 80px;">撤销</button>
      </div>
      <!-- 表单 -->
      <div>
        <div id="Hometable">
          <!-- 表单头部 -->
          <div>
            <div id="Hometabletop">
              <input style="visibility:hidden" type="checkbox">
              <div class="topone">姓名</div>
              <div class="topone">年龄</div>
              <div class="topone">性别</div>
              <div class="topone">联系电话</div>
              <div class="toptwo">详细住址</div>
              <div class="topthree">操作</div>
            </div>
          </div>
          <!-- 表单信息 -->
          <div id="Hometablebody">
                <div v-show="p.show !== 1" v-for="(p,index) in newscontent" class="body" :key="p.id" >
                  <input @click="Che" :name="'Home'+p.id" class="tablechoose" type="checkbox" value="1" :key="p.id">
                  <div class="tablename">{{p.name}}</div>
                  <div class="tableage">{{p.age}}</div>
                  <div class="tablesex">{{p.sex}}</div>
                  <div class="tablephone">{{p.phone}}</div>
                  <div class="tableaddress">{{p.address}}</div>
                  <div class="tablehandle" :name="index">
                    <button @click="Homeedit">编辑</button>
                    <button @click="HomeDele" :ref="'Home'+p.id">删除</button>
                  </div>
                </div>

          </div>
        
        </div>
      </div>
      <div id="Hometop">
        <button @click="HomeallDele">批量删除</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:"Home",
    data() {
        return {
            Homepanduan:"",
            namecontent:"",
            agecontent:0,
            sexcontent:"",
            phonecontent:"",
            addresscontent:"",
            idcontent:"",
            newscontent:[{name:"张三",age:"18",sex:"男",phone:15219172856,address:"广东省广州市花都区新华街",id:1},{name:"李三",age:"30",sex:"男",phone:18219172856,address:"广东省广州市天河区东路",id:2},{name:"王女士",age:"20",sex:"女",phone:13219172856,address:"广东省广州市番禺区北路",id:3}],
            news:[{name:"张三",age:"18",sex:"男",phone:15219172856,address:"广东省广州市花都区新华街",id:1},{name:"李三",age:"30",sex:"男",phone:18219172856,address:"广东省广州市天河区东路",id:2},{name:"王女士",age:"20",sex:"女",phone:13219172856,address:"广东省广州市番禺区北路",id:3}],
            idnumber:4,
            newsKepp:[],
            Homeitems:{},
            Homeitems2:{},
            HomeAll:[],
            Alldele:0,
            Alldele2:0,
            Homemassge:"",
            shousuoText:[""],
            createaddress:"番禺区",
            isActive:1,
        }
    },
    // 这里偷偷存东西在浏览器上
    created() {
            if (localStorage.getItem("keyname")) {
              this.news = JSON.parse(localStorage.getItem("keyname"))
              this.newscontent = this.news
            }else{
              localStorage.setItem("keyname",JSON.stringify(this.news))
            }
            if (localStorage.getItem("keyid")) {
              this.idnumber = JSON.parse(localStorage.getItem("keyid"))
            }else{
              localStorage.setItem("keyid",JSON.stringify(this.idnumber))
            }
            },
    methods: {
        Homecanceldelete(){
          this.$refs.refHomeshade.style.display = "none";
          this.namecontent = "";
          this.Homepanduan = "";
          this.agecontent = "";
          this.sexcontent = "";
          this.phonecontent = "";
          this.addresscontent = "";
        },
        Homecancel(){
          if(this.Homepanduan == "新建"){
            if(this.namecontent !== "" && this.phonecontent !== "" && this.sexcontent!== "" && this.addresscontent !== "" && this.agecontent !== "" ){
            this.newsKepp.push({name:"新建",content:{items:this.newscontent.length}})
            this.news.push({name:this.namecontent.slice(0,5),age:this.agecontent,sex:this.sexcontent,phone:this.phonecontent.slice(0,16),address:"广东省广州市"+this.createaddress+this.addresscontent,id:this.idnumber++})
            this.newscontent = this.news
            this.$refs.refHomeshade.style.display = "none";
            this.namecontent = "";
            this.agecontent = "";
            this.sexcontent = "";
            this.phonecontent = "";
            this.addresscontent = "";
            // 更新浏览器缓存
          localStorage.setItem("keyname",JSON.stringify(this.news))
          localStorage.setItem("keyid",JSON.stringify(this.idnumber))
          }else{
            alert("你有信息还没填呢，(随便填满就行，这里就不写太多检查了 不然浪费你的时间)")
          }
          }else if(this.Homepanduan == "编辑"){
            this.newsKepp.push({name:"编辑",content:{name:this.Homeitems.name,age:this.Homeitems.age,sex:this.Homeitems.sex,phone:this.Homeitems.phone,address:this.Homeitems.address,items:this.Homeitems2}})
            this.Homeitems.name = this.namecontent.slice(0,5)
            this.Homeitems.sex = this.sexcontent
            this.Homeitems.age = this.agecontent
            this.Homeitems.phone = this.phonecontent
            this.Homeitems.address = "广东省广州市"+this.createaddress+this.addresscontent
            this.newscontent = this.news
            this.$refs.refHomeshade.style.display = "none";
            this.namecontent = "";
            this.sexcontent = "";
            this.agecontent = "";
            this.phonecontent = "";
            this.addresscontent = "";
            // 更新浏览器缓存
          localStorage.setItem("keyname",JSON.stringify(this.news))
          localStorage.setItem("keyid",JSON.stringify(this.idnumber))
          }

          },
        //点击新建
        Homecreat(){
          this.Homepanduan = "新建"
          this.isActive = 1;
          this.$refs.refHomeshade.style.display = "block";
        },
        // 编辑
        Homeedit(e){
          this.$refs.refHomeshade.style.display = "block";
          let x = e.target.parentNode.getAttribute("name")
          let Y = this.news[x]
          this.namecontent = Y.name;
          this.Homepanduan = "编辑"
          this.sexcontent = Y.sex;
          this.agecontent = Y.age;
          this.phonecontent = Y.phone;
          //检查地址
          let reg = new RegExp("番禺区","ig")
          if (reg.test(Y.address)) {
            this.addresscontent = Y.address.slice(9);
            this.isActive = 1;
          }
             reg = new RegExp("越秀区","ig")
          if (reg.test(Y.address)) {
            this.addresscontent = Y.address.slice(9);
            this.isActive = 2;
          }
             reg = new RegExp("花都区","ig")
          if (reg.test(Y.address)) {
            this.addresscontent = Y.address.slice(9);
            this.isActive = 3;
          }
             reg = new RegExp("海珠区","ig")
          if (reg.test(Y.address)) {
            this.addresscontent = Y.address.slice(9);
            this.isActive = 4;
          }
             reg = new RegExp("天河区","ig")
          if (reg.test(Y.address)) {
            this.addresscontent = Y.address.slice(9);
            this.isActive = 5;
          }
          //
          this.Homeitems = Y;
          this.Homeitems2 = x;
        },
        //地址选择栏
        createaddressfn(e){
          this.createaddress = e.target.outerText
          if (this.createaddress == "番禺区") {
            this.isActive = 1
          }
          if (this.createaddress == "越秀区") {
            this.isActive = 2
          }
          if (this.createaddress == "花都区") {
            this.isActive = 3
          }
          if (this.createaddress == "海珠区") {
            this.isActive = 4
          }
          if (this.createaddress == "天河区") {
            this.isActive = 5
          }
        },
        // 删除
        HomeDele(e){
          if(this.Alldele == 0){
          this.newscontent = this.news
          let x = e.target.parentNode.getAttribute("name")
          console.log(x)
          let Y = this.news[x]
          this.newsKepp.push({name:"删除",content:{name:Y.name,age:Y.age,sex:Y.sex,phone:Y.phone,address:Y.address,items:x,id:this.news[x].id}})
          this.news.splice(x,1)
          this.newscontent = this.news
            // 更新浏览器缓存
          localStorage.setItem("keyname",JSON.stringify(this.news))
          localStorage.setItem("keyid",JSON.stringify(this.idnumber))
          }else{
          let x = e.target.parentNode.getAttribute("name")
          console.log(x)
          let Y = this.news[x]
          this.newsKepp.push({name:"删除",content:{name:Y.name,age:Y.age,sex:Y.sex,phone:Y.phone,address:Y.address,items:x,id:this.news[x].id}})
          this.news.splice(x,1)
          this.newscontent = this.news
            // 更新浏览器缓存
          localStorage.setItem("keyname",JSON.stringify(this.news))
          localStorage.setItem("keyid",JSON.stringify(this.idnumber))
          //批量
          if(this.Alldele > 0){
          this.Alldele--
          this.Alldele2++
          if (this.Alldele == 0) {
          this.newsKepp.push({name:"批量删除",content:{items:this.Alldele2}})
          this.Alldele2 = 0
          }
          }
          }
        },
        //多选框
        Che(e){
          if(e.target.checked == true ){
          }else if(e.target.checked == false){
          }
        },
        //批量删除
        HomeallDele(e){
          let x = document.querySelectorAll('.tablechoose')
          let p = x.length
          for (let i = 0; i < p; i++) {
            if(x[i].checked == true){
          this.Alldele++
          let Y = x[i].getAttribute("name")
          setTimeout(()=>{
            this.$refs[Y][0].click()
          },10)
          }
          }
        },
        // 搜索框
        shousuo(e){
          let n = this.shousuoText.length
          if (this.shousuoText[n-1] !== this.Homemassge) {
          this.newsKepp.push({name:"关键字",content:{}})
          this.shousuoText.push(this.Homemassge)
          let reg = new RegExp(this.Homemassge,"ig")
          let s = this.news.length
          for (let i = 0; i < s; i++) {
          if (reg.test(this.news[i].name) || reg.test(this.news[i].address) || reg.test(this.news[i].age) || reg.test(this.news[i].phone) || reg.test(this.news[i].sex)) {
            this.news[i].show = 0
          }else{
            this.news[i].show = 1
          }
        }
          if (this.Homemassge == "") {
              for (let i = 0; i < s; i++) {
                  this.news[i].show = 0
            }
          }
          this.newscontent = this.news
          this.$forceUpdate()
          }
        },
        // 撤回
        Homerevoke(){
          if(this.newsKepp.length !== 0){
            let x = this.newsKepp.pop();
            // 新建
            if(x.name == "新建"){
              this.news.pop()
              this.newscontent = this.news
            }
            if(x.name == "编辑"){
              let U = x.content.items;
              this.news[U].name = x.content.name
              this.news[U].sex = x.content.sex
              this.news[U].age = x.content.age
              this.news[U].phone = x.content.phone
              this.news[U].address = x.content.address
              this.newscontent = this.news
            }
            if(x.name == "删除"){
              let U = x.content.items;
              let C = {name:x.content.name,age:x.content.age,sex:x.content.sex,phone:x.content.phone,address:x.content.address,id:x.content.id}
              this.news.splice(U,0,C)
              this.newscontent = this.news
            }
            if(x.name == "批量删除"){
            let G = x.content.items
              for (let i = 0; i < G; i++) {
              x = this.newsKepp.pop();
              let U = x.content.items;
              let C = {name:x.content.name,age:x.content.age,sex:x.content.sex,phone:x.content.phone,address:x.content.address,id:x.content.id}
              this.news.splice(U,0,C)
              this.newscontent = this.news
              }
            }
            if (x.name == "关键字") {
          this.shousuoText.pop()
          let n = this.shousuoText.length
          this.Homemassge = this.shousuoText[n-1]
          let reg = new RegExp(this.Homemassge,"ig")
          let s = this.news.length
            for (let i = 0; i < s; i++) {
          if (reg.test(this.news[i].name) || reg.test(this.news[i].address) || reg.test(this.news[i].age) || reg.test(this.news[i].phone) || reg.test(this.news[i].sex)) {
              this.news[i].show = 0
            }else{
              this.news[i].show = 1
            }
          }
            if (this.Homemassge == "") {
                for (let i = 0; i < s; i++) {
                    this.news[i].show = 0
              }
            }
            this.newscontent = this.news
            this.$forceUpdate()
            }
            }
          // 更新浏览器缓存
          localStorage.setItem("keyname",JSON.stringify(this.news))
          localStorage.setItem("keyid",JSON.stringify(this.idnumber))
        },
        
    },
}
</script>

<style>
  #Homebody{
    height: 100px;
    width: 90%;
    margin: 20px auto;
  }
  #uesrone{
    line-height: 80px;
    width: 100%;
    text-align: center;
    background: #eee;
    margin: 0;
  }
  /* 遮罩层 */
   #shade{
    position: fixed;
    left: 0px;
    top: 88px;
    background: #000;
    width: 100%;
    height: 100%;
    opacity: 0.4;
   }
   #tankuang{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    width: 45%;
    height: 60%;
    border-radius: 5px;
    margin: 10% auto;
   }
          /* 信息 */
          #message{
            margin: 2% auto;
            width: 80%;
            height: 80%;
          }
          /* 选择的区域 */
          .AddressActive{
            background-color: #eee
          }
          .Bcactive:hover{
            color: #fff;
            background-color:#6495ed;
          }
  /* 表单头部 */
  #Hometabletop div {
    display: inline-block;
  }
  #Hometabletop{
    background: #eee;
    line-height: 30px;
    margin-top: 5px;
  }
  #Hometabletop .topone{
    width: 10%;
    text-align: center;
  }
  #Hometabletop .toptwo{
    width: 48%;
    text-align: center;
  }
  #Hometabletop .topthree{
    width: 10%;
    text-align: center;
  }
  /* 表单身体 */
  #Hometablebody .body div{
      display: inline-block;
    }
  #Hometablebody .body{
    padding-top: 5px;
    height: 35px;
    line-height: 30px;
    justify-content:space-between;
    border-left: 2px solid #eee;
    border-right: 2px solid #eee;
    border-bottom: 2px solid #eee;
  }
  .tablechoose{
    width: 1%;
  }
  .tablename,.tableage,.tablesex{
    width: 6%;
    margin: 0 2%;
    text-align: center;
    background: #eee;
  }
  .tablephone{
    font-size: 10px;
    width: 8%;
    margin: 0 1%;
    text-align: center;
    background: #eee;
  }
  .tableaddress{
    width: 40%;
    margin: 0 4%;
    background: #eee;
    text-align: center;
  }
  .tablehandle{
    width: 10%;
    text-align: center;
  }
  .tablehandle button{
    margin: 0 4px;
  }
  /* 批量删除 */
  #Hometop{
    margin: 5px 0;
  }
</style>